body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.jumbotron {
    width: 100%;
    min-height: 300px;
    padding-top: 100px;
    margin: 0px;
    margin-bottom: 5%;
    display: table;
    padding: 180px 20px;
    color: #ffffff;	    
    background-image: url("https://cottagelife.com/wp-content/uploads/2018/09/shutterstock_522019972-1200x744.jpg");
    background-size: cover;
	background-attachment: fixed;
}
div .thumbnail {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
    width: 100%;
    padding-top: 100%;
  }
#message {
    margin-bottom: 10%;
}
.btn {
    cursor: pointer;
    background-color: #000000;
    padding: 5px 10px 5px 10px;
    border-radius: 10%;
    margin-right: 5px;
}

#btn {
    text-decoration: none;
    color: rgb(255, 255, 255);

}

#btn:hover {
    color: #e74944;
    background-color: #000000;
}
.btn {
    cursor: pointer;
    color: rgb(255, 255, 255);
    background-color: #000f82;
}

.btn:hover {
    color: #e7c644;
    background-color: #000000;
}

#search, #saved {
    color: #010101;
}

#search:hover, #saved:hover {
    color: #4444e7;
}

#nav {
    background-color: rgb(255, 247, 0);
}
